.center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}
.form {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    color: white;
  }
  .form > form {
    width: 100%;
    max-width: 300px;
    text-align: center;
    background: #eee;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.4);
    padding: 15px;
    border-radius: 5px;
  }